<template>
  <div class="all-container" >
    <div class="topMenu-container" :style="{filter:  $store.state.hasBlur ? 'blur(6px)' : 'none'}">
       <div class="header-container">
         <div class="top-menu">
           <div class="logoDiv">
             <div class="logoContainer">
               <div class="div_img"></div>
               <div class="logoWord">
                 <span>{{proName}}</span>
               </div>
             </div>
           </div>
         </div>
         <div class="center-menu">
            <ul  class="myMenu" v-if="allMenu.length > 0">
              <li v-for="(menu, $index) in allMenu" :class="{'selectedPLi': menu.menuId == $store.state.topMenu ,'parentLi': true}" @click="showSecondMenu(menu, $index)" >
                <div style="height:100%;display: table-cell;vertical-align: middle;line-height: 3">
                    <img class="fartherImg" :src="'dist/img/menuIcon/one/'+ menu.menuIcon + '.png'" >
                    <span>{{menu.menuTitle}}</span>
                </div>
             </li>
           </ul>
         </div>
       </div>
       <div class="childMenu-container">
         <template v-if="allMenu.length > 0 && allMenu[$store.state.currentParIndex].children && allMenu[$store.state.currentParIndex].children.length > 0" >
          <ul  class="myMenu" >
            <li  v-for="(menu, $index) in allMenu[$store.state.currentParIndex].children"
              :class="{'selectedLi': false && (menu.menuId == $store.state.pMenu), 'parentLi': true}" @click="noChildPushMenu(menu)">
               <div class="menuInfo-container"   @mouseover="showThirdMenu(menu, $index)" @mouseout="hideMenu"
                  @mousedown="showThirdMenu(menu, $index)">
                  <div class="menuDiv">
                    <img class="fartherImg" :src="'dist/img/menuIcon/two/'+ menu.menuIcon + '.png'" >
                    <span>{{menu.menuTitle}}<i class="hasIcon"  style="float: right;margin:0.2em 0 0 0.3em;" v-if="menu.children && menu.children.length > 0"><icon name="caret-down"></icon></i></span>
                  </div>
                  <div class="containerLeaf" v-if="menu.children && menu.children.length > 0">
                     <ul class="leafMenu" v-show="menu.children && menu.children.length > 0 && menu.menuId == $store.state.pMenu && showLeaf"
                      :style="{padding: menu.children && menu.children.length > 1 ? '0.8em 0' : '0'}"
                      @mouseout="hideMenu">
                      <li v-for="(child, $leafIndex) in menu.children" class="parentLi"  @click.stop="pushMenu(menu, child, $leafIndex, 0)">
                        <img class="fartherImg" :src="'dist/img/menuIcon/three/'+ child.menuIcon + '.png'" >
                        <span>{{child.menuTitle}}</span>
                      </li>
                    </ul>
                  </div>
               </div>
            </li>
          </ul>
         </template>
         <div class="staff-container">
            <i class="hasIcon hasImg alarmImg"  title="告警信息" @click="sendMessage">
                <img src="dist/img/face/message-icon.png" />
                <label class="alarmNum">{{alarm}}</label>
                <!--<icon name="commenting" scale="1"></icon>-->
            </i>
            <span class="detailInfo-span">欢迎你，<span class="hoverStaff" @click="changePassword">{{userName}}</span></span>
            <span class="detailInfo-span" @click="downloadInfo">资料下载</span>
            <span class="detailInfo-span" @click="powerOff">退出系统</span>
         </div>
       </div>
    </div>
    <div class="mainBody-container" :style="{filter:  $store.state.hasBlur ? 'blur(6px)' : 'none'}">
      <div class="main-container" >
        <div class="top-container" v-if="selectTab.length > 1">
             <!--  <i class="hasIcon" v-show="prev" @click="backMove"><icon   name="angle-left" style="float: left;font-size: 16px" scale="1.8"></icon></i> -->
              <div ref="holdTab" class="label-container" :style="{marginLeft: prev ? 0: '0.1em'}">
                <ul class="tabUl" ref="tabUl">
                  <li  ref="tabLI" v-show="!tabs.isDel"  :key="$index" v-for="(tabs, $index) in selectTab"
                   :style="{width: $index  === 0 ? '6em' : 'calc((100% - 6em) / ' + (selectTab.length - 1) + ')'}"
                   :class="{selectedTab: tabs.menuId == ($store.state.cMenu || sessionStorage.cMenu || -1)}" @click="pushMenu(tabs.parentMenuId, tabs, tabs.pIndex,1)" @contextmenu.prevent=" $index !== 0 && showContextMenu($event, $index)" @blur="closeContextMenu" tabindex="1">
                    <template v-if="$index === 0">
                      <a ><i><icon name="home" scale="1"></icon></i></a>
                    </template>
                    <template v-else>
                      <a :title="tabs.menuTitle">
                        <span class="menuLabel">{{tabs.menuTitle}}</span>
                        <i  v-if="selectTab.length > 1 && tabs.name !== 'Face'" class="removeTab" @click.stop="removeTab($index)">
                          <img src="dist/img/face/close-icon.png" style="width:100%;height:100%">
                        </i>
                      </a>
                    </template>
                  </li>
                </ul>

              </div>
            <!--   <i @click="moveOn" v-show="next" class="hasIcon"><icon  name="angle-right" style="float: right;font-size: 16px" scale="1.8"></icon></i> -->
         </div>
        <div class="bottom-container" :class="{'center--noTop-container': selectTab.length <= 1}">

        <div class="rightCon"
             :class="{toggleRDiv: false, noTreeContainer: true}"
              >
          <div class="right-container">
            <div :class="{'center-container' : selectTab.length > 1} " style="height:100%">
              <template v-for="(tab, $index) in selectTab"  v-if="!tab.isDel" v-show="tab.menuId == ($store.state.cMenu || sessionStorage.cMenu)">
                <keep-alive>
                  <router-view ref="viewShow" :name="getName(tab)"></router-view>
                </keep-alive>
              </template>
            </div>
          <!--   <footer class="bottom-footer" v-html="footer + appVersion()">
            </footer> -->
            <dialogs type="alert" ref="alerts"></dialogs>
            <dialogs type="customer" ref="downLoad" >
              <div slot="header">
                资料下载
              </div>
              <div slot="content" >
                
                  <p class="downLoad-item">
                    <i class="hasIcon hasImg"  style="margin-right:0.6em; font-size: 1.3em"><icon name="download"></icon></i>
                    <a   href="dist/modal/install.rar" target="_blank" title="读卡器服务程序"> 读卡器服务程序</a>
                  </p>
                  <p class="downLoad-item">
                    <i class="hasIcon hasImg" style="margin-right:0.6em; font-size: 1.3em"><icon name="download"></icon></i>
                    <a  href="dist/modal/XP80_Driver_Setup.rar" target="_blank" title="打印机驱动"> 打印机驱动</a>
                  </p>
                  <p class="downLoad-item">
                    <i class="hasIcon hasImg"  style="margin-right:0.6em; font-size: 1.3em">
                    <icon name="download"></icon></i>
                    <a  href="http://www.adobe.com/go/getflashplayer" target="_blank" title="Flash 软件下载地址"> Flash 软件下载地址</a>
                  </p>
                  <p class="downLoad-item" @click="getQRCode">
                      <i class="hasIcon hasImg" style="margin-right:0.6em; font-size: 1.3em"><icon name="qrcode"></icon></i>
                      <a title="获取补抄App二维码">补抄App</a>
                   </p>
                </div>
              </dialogs>
              <dialogs type="customer" ref="QR" width="620px">
                <div slot="header">
                  补抄App二维码
                </div>
                <div slot="content" >
                 <ul class="dialog-ui">
                   <li class="halfWidth" >
                    <div class="hasQR">
                      <!--  <qrcode-vue :value="qrcodeValue" :size="size" level="H"  class="QRCanvas"></qrcode-vue> -->
                      <img src="dist/code.png" style="width:140px;height:140px" class="QRCanvas">
                    </div>
                    <p>1、扫描二维码下载掌机App</p>
                  </li>
                  <li class="halfWidth" >
                    <div class="hasQR">
                     <qrcode-vue :value="qrcodeValue" :size="size" level="H" class="QRCanvas"></qrcode-vue>
                   </div>
                   <p>2、打开App扫描二维码注册</p>
                 </li>
               </ul>

             </div>
            </dialogs>
            <dialogs type="customer" ref="changePass" width="36em">
              <div slot="header">
                修改密码
              </div>
              <div slot="content" >
                <ul class="dialog-ui" style="padding:1em 2em">
                  <li class="form-list allWidth" v-for="(item, $index) in changePassOpt">
                    <span class="label-show" > <span style="color:#EB3B3B">* </span>{{item.name}}</span>
                    <inputs :option="item.option" @getInput="getFrom" :ref="item.option.key"></inputs>
                  </li>
                </ul>
              </div>
              <div slot="footer">
                <button class="button-default" @click="submitChange">提交</button>
                <button class="button-default cancel-button" @click="closeChangePass">取消</button>
              </div>
            </dialogs>

          </div>
        </div>
        </div>
      </div>
    </div>
    <send ref="sends"></send>
    <loginAgain ref="loginAgain" ></loginAgain>
    <webSocket ref="webSocket" v-if="mySocket"></webSocket>
    <encryptPass ref="encrypt"></encryptPass>
      <div class="contextMenu" :style="contextMenu">
         <ul>
           <li @mousedown="removeCurrentTab">关闭当前标签页</li>
           <li @mousedown="removeAllTab">关闭其他标签页</li>
         </ul>
      </div>
  </div>
</template>
<script>
  import send from './childComponent/SendMsg.vue'
  import VuePerfectScrollbar from 'vue-perfect-scrollbar'
  import webSocket from './childComponent/webSocket.vue'
  import QrcodeVue from 'qrcode.vue'
  import loginAgain from './childComponent/loginAgain.vue'
  import 'vue-awesome/icons/qrcode'
  import 'vue-awesome/icons/home'
  import 'vue-awesome/icons/angle-left'
  import 'vue-awesome/icons/caret-down'
  import encryptPass from './childComponent/encryptPass.vue'
  export default{
    data() {
           return {
          
              contextMenu: { //浏览标签快捷键操作位置
                  display: 'none',
                  top: 0,
                  left:0
              },
              size: 140, //二维码大小
              mySocket: true,
              prev: false,
              next: false,
              hoverIndex: -1,
              menuIndex: 0,
              allMenu: [],
              //footer: 'Copyright © 深圳市本道科技有限公司 &nbsp;&nbsp;版本信息：',
              changePass: { //修改密码的参数对象
                   oldPassword: '',
                   newPassword: '',
                   confirmPassword: ''
              },
              alarm: 0, //告警数量
              overAlarm: '',
              removeIndex: -1, //当前选中的标签页下标
              currentUrl: '',
              showLeaf: false, //是否显示三级菜单
           }
     },
    methods: {
      showLoginAgain: function () {
         if(!this.$refs.loginAgain.showDialog) {
             this.$refs.loginAgain.show()
         }
      },
      noChildPushMenu: function (menu) {
        if((menu.children && menu.children.length === 0) || !menu.children) {
           this.$store.state.pMenu = sessionStorage.pMenu = menu.menuId ;
           let arr = {};
           for(let i in menu) {
              arr[i] = menu[i];
           }
           arr.parentMenuId = arr.menuId;
           this.pushMenu(menu, arr, 0, 0)
        }
      },
       //点击一级菜单显示对应的二级菜单
       showSecondMenu: function (item, index) {
         this.$store.state.topMenu = sessionStorage.topMenu = item.menuId;
         this.$store.state.currentParIndex = sessionStorage.currentParIndex = index;
         this.$store.state.pMenu = sessionStorage.pMenu = item.children[0].menuId;
       },
       //显示三级菜单
       showThirdMenu: function (menu, index) {
         this.$store.state.topMenu = sessionStorage.topMenu = menu.parentMenuId;
         this.$store.state.pMenu = sessionStorage.pMenu = menu.menuId ;
         this.showLeaf = true;
       },
       hideMenu() {
         this.showLeaf = false;
       },



       removeAllTab: function () {
        // let tab = this.$store.state.selectTab;
         let arr = [];
         arr.push(this.$store.state.selectTab[0]);
         let current = this.$store.state.selectTab[this.removeIndex];
         arr.push(current);
         this.$store.state.selectTab = arr;
         sessionStorage.selectTab = JSON.stringify(arr);
         this.pushMenu(current.parentMenuId,current, current.pIndex, 1)
       },
       removeCurrentTab: function () {
         if(this.removeIndex !== -1) {
            this.removeTab(this.removeIndex)
         }
       },
       closeContextMenu: function() {
          this.contextMenu.display = 'none';
          this.removeIndex = -1;
       },
       showContextMenu: function (event, index) {
          this.contextMenu.left = (event.clientX + 2) + 'px';
          this.contextMenu.top = (event.clientY + 2) + 'px';
          this.contextMenu.display = 'block';
          this.removeIndex = index;
       },
       getQRCode: function () {
          this.$refs.QR.show({
            hasClose: true
          });
       },
       appVersion: function () {
          return (localStorage.systemConfig ?  JSON.parse(localStorage.systemConfig).version : '1.0.0') + '.11';
       },
       getAlarm: function () {
         let that = this;
         this.getRequest({
           url: 'first/alarmUserCnt',
           method: 'post',
           success: function (data) {
             that.alarm  = that.$store.state.alarmNum = data.data || 0
           }
          });
       },
       reStartSocket: function () {
          let that = this;
          that.mySocket = false;
          that.$nextTick(function ()  {
             that.mySocket = true
          });
       },
       getAlarms: function () {
         let that = this;
         this.$store.state.linkCheck = sessionStorage.userId;

         this.$store.state.callback['alarm'] = {
           key: 'alarm',
           success: function (data) {
                  that.alarm = that.$store.state.alarmNum = data.data || 0
                }
            }
       },
       getName: function (tab) {

          return tab.menuUrl ? tab.menuUrl.split('/')[2] : '';
       },
       sendMessage: function () {
         let that = this;
         that.$refs.sends.show();
       },
       submitChange: function () {
         let that = this;
         that.regularTest({
            emptyKey: ['changePass.oldPassword', 'changePass.newPassword', 'changePass.confirmPassword'],
            callback: function() {
              if (that.changePass.newPassword === that.changePass.confirmPassword ){
                let oldPass = that.$refs.encrypt.encryptPass(that.changePass.oldPassword);
                let newPass = that.$refs.encrypt.encryptPass(that.changePass.newPassword);
                that.getRequest({
                  url: 'user/updatePassword',
                  param: {
                    oldPassword: oldPass,
                    newPassword: newPass
                  },
                  method: 'post',
                  success: function (data) {
                    that.closeChangePass();
                    that.$refs.alerts.show({msg: data.message})
                  },
                  error: function (data) {
                    that.$refs.alerts.show({msg: data.message})
                  }
                }, true)
              } else {
                that.$refs.alerts.show({msg: '两次输入的密码不一致'})
              }
            }
         })
       },
       getFrom: function (val) {
        if (val.combine) {
            this.searchParam = this.cleanSearchData(this.searchParam);
        }
          this.getOption(val, true);
       },
       changePassword: function () {
         this.$refs.changePass.show();
       },
       downloadInfo: function () {
          let that = this;
          this.$refs.downLoad.show({
            hasClose: true,
            action: {
               cancel: function () {
                   that.closeDialog();
               }
            }
         });
       },
       closeChangePass: function () {
         this.changePass = this.cleanSearchData(this.changePass);
         this.$refs.changePass.close();
       },
       closeDialog: function () {
          this.$refs.downLoad.close();
       },
       pushMenu(pMenu, cMenu, index, flag) {
         this.hoverIndex = -1;
         let n = 0;
         for (let m = 0; m < this.selectTab.length ; m++) {
           if (!this.selectTab[m].isDel) {
             n = n + 1;
           }
         }
         if (n < 100) {
           this.$store.state.cMenu = sessionStorage.cMenu = cMenu.menuId;
           this.$store.state.pMenu = sessionStorage.pMenu = pMenu.menuId || cMenu.parentMenuId;
           this.$store.state.topMenu = sessionStorage.topMenu = pMenu.parentMenuId || cMenu.grandPMenuId;
           this.$store.state.hasTree = sessionStorage.hasTree = cMenu.hasTree;
           this.currentUrl = cMenu.menuUrl;
           if (flag === 0 ) {
                 cMenu.grandPMenuId = pMenu.parentMenuId;
                 let replace = {};
                 for(let item in cMenu) {
                   replace[item] = cMenu[item]
                 }
                 replace['isDel'] = false;
                 replace['pIndex'] = index;
                 let tabs = this.$store.state.selectTab;
                 let length = tabs.length - 1;
                 for(let j=length; j> -1; j--){
                    if (tabs[j].isDel) {
                     tabs.splice(j, 1);
                     break;
                    }
                 }
                 let newTab = this.$store.state.selectTab;
                for(let i=0; i< newTab.length; i++) {
                   let current = newTab[i];
                   if (!current.isDel && current.menuId === cMenu.menuId) {
                     this.$store.state.currentParIndex = sessionStorage.currentParIndex =  current.grandIndex;
                     this.$router.push(current.menuUrl);
                     break;
                   } else {
                      if (i === newTab.length - 1) {
                         cMenu.grandIndex = replace.grandIndex = this.$store.state.currentParIndex;
                         newTab.push(replace);
                         this.$router.push(replace.menuUrl);
                         let that = this;
                         this.$nextTick(function () {
                            that.resetMove()
                         });
                      }
                   }
                 }
                 sessionStorage.selectTab = JSON.stringify(this.$store.state.selectTab);
                 this.showLeaf = false;
            } else {
                 if(cMenu.menuId === -1) {
                   this.$store.state.currentParIndex = sessionStorage.currentParIndex = 0;
                 } else {
                    this.$store.state.currentParIndex = sessionStorage.currentParIndex =  cMenu.grandIndex;
                 }
                 this.$router.push({path: cMenu.menuUrl});
                 this.currentUrl = cMenu.menuUrl;
             }
           } else {
             if (flag === 1 || flags) {
                this.$store.state.currentParIndex = sessionStorage.currentParIndex =  cMenu.grandIndex;
                this.$router.push({path: cMenu.menuUrl});
                this.currentUrl = tab.menuUrl;
              } else {
                this.$refs.dialog1[0].show({
                  msg: '最多只能同时打开8个窗口，请先关闭无用窗口再进行操作'
                })
             }
          }
       },
       removeTab(index) {
         let tabs = this.$store.state.selectTab;
         if (tabs.length > 1)  {
           length = tabs.length - 1;
           if (index === length) {
             tabs.splice(index, 1);
             sessionStorage.selectTab = JSON.stringify(tabs)
           } else {
             this.$set(tabs[index], 'isDel', true)
           }
           for (let i= tabs.length - 1; i>=0;i--) {
             if (tabs[i].isDel) {
               tabs.splice(i, 1);
             } else {
               sessionStorage.selectTab = JSON.stringify(tabs);
               break;
             }
           }
           let item = tabs[tabs.length - 1];
           this.$nextTick(function () {
             this.resetMove()
           });
           if(this.currentUrl !== item.menuUrl) {
             this.pushMenu(item.parentMenuId, item, item.pIndex, 1);
           }
           this.$store.state.downloadLink = localStorage.downloadLink = '';
         }
       },
       resetMove: function() {
          let container;
          let content;
          if (this.$refs.holdTab) {
             container = this.$refs.holdTab.clientWidth;
          }
          if (this.$refs.tabUl) {
             content = this.$refs.tabUl.scrollWidth;
           }
          let leftMove = content - container;
          if (leftMove> 0) {
            this.prev = true;
            this.next = true;
            this.$refs.holdTab.scrollLeft = leftMove
          } else {
            this.prev = this.next = false;
          }
        },

       moveOn: function () {
         let width = this.$refs.tabLI[1].clientWidth;
         let right = this.$refs.tabUl.scrollLeft;
         let container = this.$refs.holdTab.clientWidth;
         let content = this.$refs.tabUl.scrollWidth;
         let leftMove = content - container;
         if ((leftMove-right )< width) {
           this.$refs.holdTab.scrollLeft = leftMove;
           this.next = false;
         } else {
           this.$refs.holdTab.scrollLeft = this.$refs.holdTab.scrollLeft + width;
         }
         this.prev = true;
       },
       backMove: function () {
         let width = this.$refs.tabLI[1].clientWidth;
         let left = this.$refs.holdTab.scrollLeft;
         if (left < width) {
           this.$refs.holdTab.scrollLeft = 0;
           this.prev = false;
         } else {
           this.$refs.holdTab.scrollLeft = this.$refs.holdTab.scrollLeft - width;
         }
         this.next = true;
       },
       getDates() {
         let date = new Date();
         return date.getFullYear() + '-' + ((date.getMonth() + 1) > 9 ? (date.getMonth() + 1) : ('0' + (date.getMonth() + 1))) + '-' + (date.getDate() > 10 ? date.getDate() : ('0' + date.getDate()))
       },
       getMenu(index) {
           let that = this;
           if (index === 'undefined') {
             that.menuIndex = index || 0;
             that.hoverIndex = -1;
           } else {
             that.menuIndex =  that.hoverIndex = index
           }
           this.getRequest({
              url: 'menu/queryMenusByUserRole',
              method: 'post',
              // url: 'dist/json/newMenu.json',
              // method: 'get',
              success: function(data) {
               that.allMenu = data.data || [];
             }
           });
       },
       showMenu(event, index) {
          this.hoverIndex = index;
       },

       powerOff() {
           let that = this;
           that.clearData();
           that.getRequest({
             url: 'logout',
             method: 'post',
             success: function () {
               that.$refs.webSocket.pageOver();
               that.$router.push({path: '/'});
             },
             error: function () {
              that.$refs.webSocket.pageOver();
               that.$router.push({path: '/'});
             }
           });
           that.$refs.webSocket.pageOver();
           that.$router.push({path: '/'});
       },
       clearData() {
         sessionStorage.clear();
         localStorage.systemConfig = '';
         localStorage.userInfo = '';
         clearInterval(this.overAlarm);
         this.overAlarm = '';
       }
    },
    components: {
       send,
       VuePerfectScrollbar,
       webSocket,
       QrcodeVue,
       loginAgain,
       encryptPass
    },
    computed: {
        qrcodeValue: function () {
              let WbUrl = this.$store.state.socketUrl;
              let urlArr = WbUrl.split(':');
              switch(urlArr[0]) {
                 case 'ws':
                   return WbUrl.replace('ws', 'http') + '/';
                 case 'wss':
                  return WbUrl.replace('ws', 'https') + '/';
              }
            },
        footer: function () {
            let name = '';
            if (localStorage.copyright !== null &&  localStorage.copyright !== 'undefined' &&  typeof localStorage.copyright !== 'undefined') {
              name = localStorage.copyright
            }
            return 'Copyright © '+ (this.$store.state.copyright !== '' ? this.$store.state.copyright : name) +' &nbsp;&nbsp;版本信息：';
        },
        proName: function () {
                let name = '';
                if (localStorage.SystemName !== null &&  localStorage.SystemName !== 'undefined' &&  typeof localStorage.SystemName !== 'undefined') {
                    name = localStorage.SystemName
                }
                return this.$store.state.sysName !== '' ? this.$store.state.sysName : name
        },

        userName: function () {
          return localStorage.userInfo ?  JSON.parse(localStorage.userInfo).userName : '';
        },
        selectTab: function () {
          return this.$store.state.selectTab || JSON.parse(sessionStorage.selectTab)
        },
        cMenu: function () {
          return this.$store.state.cMenu || sessionStorage.cMenu
        },
        changePassOpt: function () {
           let width= 'calc(100% - 5.5em)';
           return  [
             {
               name: '旧密码',
               option: {
                 type: 'passwordInput',
                 width: width,
                 key: 'changePass.oldPassword',
                 placeHolder: '请输入旧密码',
                 value: this.changePass.oldPassword,
                 errorMsg: '允许输入数字，字母，特殊字符（!@#$%^&*），至少5位',
                 regular:'password',
                 length: 22
               }
             },
             {
               name:'新密码',
               title: '提示: 长度为5到22位',
               option: {
                 type: 'passwordInput',
                 width: width,
                 key: 'changePass.newPassword',
                 placeHolder: '请输入新密码',
                 regular: 'password',
                 errorMsg: '允许输入数字，字母，英式特殊字符（! @ # $ % ^ & *），至少5位',
                 value: this.changePass.newPassword,
                 length: 22
               },

             },
             {
               name: '确认密码',
               title: '提示: 长度为5到22位',
               option:  {
                 type: 'passwordInput',
                 width: width,
                 key: 'changePass.confirmPassword',
                 placeHolder: '请再次输入新密码',
                 regular: 'password',
                 errorMsg: '允许输入数字，字母，英式特殊字符（! @ # $ % ^ & *），至少5位',
                 value: this.changePass.confirmPassword,
                 length: 22
               }
             }
           ]
        }
    },
    created: function () {
        this.getMenu();
    },
    mounted: function () {
       let that = this;
       that.isIn();
       that.getAlarm();
       that.resetMove();
       that.overAlarm = setInterval(function () {
          that.getAlarms();
       }, 60000);
       window.onresize = function() {
          that.$store.state.windowWidth = document.body.clientWidth;
          that.$store.state.windowHeight = document.body.clientHeight
        }
    },
    watch: {
      searchType: {
        handler(val) {
         // this.$store.state.currentSearchType = val;
        }
      }
    }
  }
</script>

