        .label-span+.label-span, .label-span+span,  .label-span+ label{
          text-align: left;
          max-width:  calc(100% - 8em);
          overflow: hidden;
          text-overflow: ellipsis;
          white-space: nowrap;
          min-width:1em
        }
        .myInput input,.myInput textarea, .mySelect button, .date-picker button,  .dateTimer button,.inputBtns{
          font-size: 0.83em;
          height: 2.9em;
        }
        .date-picker button{
          line-height: 1.5;
        }
      }
  }
  .form-list:after{
    @extend .clearElement
  }
  /*
    没有边框和背景的button，包含svg图标
  */
  .list-operate{
    background: transparent;
    border:0;
    margin-top:-0.4em;
    color: #69A4E7;
    text-decoration: underline;
    letter-spacing: 0.05em;
    .hasIcon{
      svg{
        font-size: 0.875em;
        width: 1.8em;
        height: 1.6em;
      }
    }
  }
  .list-operate:hover{
    color: white
  }
  .top-hover{
    position: absolute;
    top:0;
    right:0;
    width: 100%;
    height:100%;
    background: linear-gradient(to bottom left, rgba(43,78,106, 1), rgba(23,38,61, 1) 25%);
  }
  .downLoad-item{
    color: white;
    text-align:left;

    a{
      color: white;
    }
    a:hover{
      color:#6EB6E6;
      text-decoration: underline;
    }
  }


  .alarmNum{
    display: block;
    min-width: 1.7em;
    min-height: 1.7em;
    background: #EC3935;
    color: white;
    position: absolute;
    border-radius: 50%;
    font-size: 0.7em;
    text-align: center;
    line-height: 1.7;
    top: -0.3em;
    right: -0.4em;
    font-style: normal;
  }
  .myTable{
    tbody{
      .list-operate{
        font-size: 0.94em;
        height: 2.207em;
        margin-top: 0;
      }
    }
  }
  .btn-default{
    background: transparent!important;
  }
  .hasLabel{
    label{
      height: 100% ;
      line-height: 2.875em;
      display: block;
    }
  }
  .hasQR{
    background:white;
    width: 160px;
    margin:1em calc(50% - 80px);
    .QRCanvas{
      padding:0.6em;
    }
  }
  .contextMenu{
    position: absolute;
    z-index: 10;
    font-size: 0.82em;
    background: white;
    color: black;
    border-radius: 0.1em;
    border:1px solid #E3E3E2;
    ul{
      width: 12em;
      padding: 0.4em 0;
      li {
        padding: 0.4em 1.2em;
        width: calc(100% - 2.4em);
        cursor: pointer;
        text-align:left
      }
      li:hover{
        background: #E3E3E2
      }
    }
  }
  .con-left {
        width: calc(20%);
        height:calc(100% - 2em);
        margin:0!important;
        background: rgba(#3588fa, 0.3);
        position: relative;
        float: left;
        border-radius:0.3em;
        padding: 1em 0!important;
        box-shadow: 2px 2px 3px rgba(0,0,0,0.3);
       .list-header{
        padding: 0.5em 1.2em;
        border:0;
      }
    }
  .con_center {
        position: relative;
          border-radius:0.3em;
        background: rgba(#3588fa, 0.3);
        float: left;
        font-size: 1em;
        height: calc(100% - 1.6em);
        overflow: hidden;
        margin:0 0 0 0.4em!important;
        width: calc(80% - 3.4em);
        padding: 0.8em 1.5em !important;
        box-shadow: 2px 2px 3px rgba(0,0,0,0.3);
        position:relative;
        .list-contents{
            border-radius: 0.3em;
            background:rgba(#3588fa, 0.3);
            padding: 1em 2em;
            font-size: 1em
          }
          .list-contents + .list-contents{
            margin-top:0.5em;
          }
          .list-contents:after{
            @extend .clearElement
          }
      }
  .con_center::-webkit-scrollbar {
       display:none
    }
  .con_pe {
        width: 100%;
        height: calc(100% - 9.3em);
        line-height: 2.7;
        button {
          background: #3D88D8
        }
         li {
          width: calc(100% - 5.2em);
           padding:0 2em 0 3.2em;
           text-align:left;
           position: relative;
           margin: 1px 0;
           font-size:0.875em;
           cursor: pointer;
           overflow:hidden;
           white-space:nowrap;
           text-overflow:ellipsis;
           display: block;
        }
        li:after{
          content: '';
          display: block;
          width:0.5em;
          height:0.5em;
          position: absolute;
          background: #3E8BDC;
          border-radius: 50%;
          top: calc(50% - 0.25em);
          left: 1.6em;
        }
        li:hover, li.rowselect{
          background: #3E8BDB;

        }
        li:hover:after, li.rowselect:after{
          background: white
         }
        li.noHover{
          background: transparent;
          input{
            background-color: rgba(34, 127, 223, 0.2);
            border-color:#227fdf;
          }
          .btn-default{
           color: white!important;
           background: #227fdf !important;
           border:0;
          margin-top:-2px
        }
       }
       li.noHover:hover{
          background: transparent;
        }
    }


  $imgWidth: 9.2em;
  $borderWidth:  calc(#{$imgWidth} * 0.15);
   .myCustomerLoad{
      .dialogContent .customerHeader{
        background:transparent!important;
      }
      .loadingLeft{
        width: $imgWidth;
        height: $imgWidth;
        border-radius: 50%;
        background: url('../assets/img/loading/bottom.png') ;
        background-size:contain ;
        position: relative;
        float:left
      }
      .loadingRight{
        width: calc(100% - #{$imgWidth} - 2em);
        position: relative;
        float:left;
        margin-left:2em;
        right:0;
        height: calc( #{$imgWidth}  + 1em);
        line-height:  7.6;
        p {
          text-align: left;
          font-size: 1em;
          img {
            margin-right: 1em;
          }
            i {
              margin-left: -0.2em;
              margin-top: 0.3em;
              margin-right: 1.6em
            }
          }
          .showResult {
            height: 3.5em;
            overflow: hidden;
            p {
              line-height: 2.5;
              label{
                font-size: 1em;
                width: 100%;
                display: block;
              }
            }
          }

      }
      .centerShadow{
        background:  url('../assets/img/loading/inside-shadow.png') ;
        background-size:contain ;
        position: absolute;
        width:$imgWidth;
        height:$imgWidth;
        border-radius: 50%;
        z-index: 2;
        top: 0;
        span{
          font-size: 1.8em;
          text-shadow: 0 3px 5px black ;
          position: absolute;
          width:100%;
          text-align: center;
          top: 40%;
          left:0
        }
        span+span{
          top: 60%;
          font-size:1.1em;
          color:#627293
        }
        img{
          width:$imgWidth;
          height:$imgWidth;
        }
      }
      .currentPercent{
          margin: 1em;
          width: calc(#{$imgWidth} - 2em);
          height: calc(#{$imgWidth} -  2em);
          position: relative;
          top: 0;
          left:0;
          z-index: 1;
          .percentLeft{
            width: calc(#{$imgWidth} / 2  - 1em);
            height: calc(#{$imgWidth} -  1em);
            position: absolute;
            left:0;
            top: 0;
            overflow:hidden ;
          }
          .percentRight{
            width: calc(#{$imgWidth} / 2  - 1em);
            height: calc(#{$imgWidth} -  1em);
            position: absolute;
            right:0;
            top: 0;
            overflow:hidden ;
         }
        .circleProgress{
          width: calc(#{$imgWidth} - 2 *  #{$borderWidth} - 2em);
          height: calc(#{$imgWidth} - 2 * #{$borderWidth} - 2em);
          border:$borderWidth solid transparent;
          border-radius: 50%;
          position: absolute;
          top:0;
          transform: rotate(45deg);
        }
        .rightCircle{
          animation: circleProgressLoad_right 5s linear infinite;
          right:0;
        }
        .leftCircle{
          left:0;
          animation: circleProgressLoad_left 5s linear infinite;
        }
        @-webkit-keyframes circleProgressLoad_right{
          0%{
            -webkit-transform: rotate(45deg);
          }
          50%,100%{
           border-bottom: $borderWidth solid rgba(#EA4734, 1);
           border-left: $borderWidth  solid  rgba(#DD3421, 1);
            -webkit-transform: rotate(225deg);
          }
        }
        @-webkit-keyframes circleProgressLoad_left{
          0%,50%{
            -webkit-transform: rotate(45deg);
          }
          100%{
            border-top: $borderWidth solid rgba(#DD3421, 1);
            border-right: $borderWidth solid  rgba(#d0210d, 1);
            -webkit-transform: rotate(225deg);
          }
        }
      }
    }
  .fades-enter-active, .fades-leave-active {
    transition: all 1s;
  }
  .fades-enter{
    opacity: 0;
    transform: translateY(0.5em);
  }
  .fades-leave-to{
    opacity: 0.5;
    transform: translateY(-0.5em);
  }
  .result-button{
      margin:0;
       float:right;
       line-height: 1.5em;
       background: transparent;
       text-decoration: underline;
     }
   .result-button:hover{
      color: #27A5FF!important;
     }
  @media screen and (max-width: 1366px) {
      .all-container .topMenu-container .childMenu-container{
        .myMenu{
          li.parentLi{
            .menuInfo-container .menuDiv{
              padding:5px 1em
            }
          }
        }
      }
  }
  @media screen and (min-width: 1367px) and (max-width: 1700px) {
    #app {
      .center-menu{

      }
    }
  }
  @media screen and (min-width: 1701px) and (max-width: 1920px) {
  #app {
      .center-menu{

      }
    }
  }
</style>



