<template >
    <div class="myDialog" >
      <dialogs type="customer" ref="sendMsg" width="60em" maxHeight="600px">
        <div slot="header">
          当前告警用户
          <!-- <i  @click="closeDialog"><icon  name="remove" class="icon" scale="1.3"></icon></i> -->
        </div>
        <div slot="content">
          <tables ref="caution"  class="dialogTable" :tableOpt="TableOpt"  v-if="showTable"></tables>
        </div>
      </dialogs>
        <dialogs type="alert" ref="alerts"></dialogs>
        <dialogs type="confirm" ref="confirms"></dialogs>
        <dialogs type="loading" ref="loadings"></dialogs>
    </div>
</template>
<script>
import 'vue-awesome/icons/envelope'
    export default{
        data () {
            return {
                cursor: 'default',
                styleParams: '',
                sendFlag: '',
                controls: [],
                showTable: false,
                deviceType: {
                    '07': {text: '电表', text1: '电'},
                    '08': {text: '水表', text1: '水'},
                    '09': {text: '气表', text1: '气'}
                },
                sendData: {
                    // 短信发送信息
                    customerNo: "",
                    customerName: "",
                    deviceType: "",
                    deviceNo: "",
                    readValue: "",
                    collectDate: "",
                    companyName: "",
                    paymentAddr: "",
                    customerAddr: '',
                    alarmThreshold:'',
                    installAddr: ''
                },
                //当前操作的欠费行
                arrearageRow: {
                    rowData: {},
                    rowIndex: 0,
                },
                TableOpt: {
                    rowName: 'data',
                    column: [
                        {title: '用户姓名', width: '120', field: 'customerName', textAlign: 'center'},
                        {title: '门牌编号', field: 'propertyName', textAlign: 'center',  width: 120},
                        // {title: '表号', width: '120', field: 'deviceNo', textAlign: 'center'},
                        {title: '余额', width: '100', field: 'readValue', textAlign: 'center', sortable: false},
                        {title: '剩余电量', width: '120', field: 'remainPower', textAlign: 'center', sortable: false},
                     
                        {title: '用户地址', width: '120', field: 'customerAddr', textAlign: 'center'},
                        {
                            title: '发短信',
                            width: '100',
                            textAlign: 'center',
                            type: 'object',
                            formatter: function (val, index, row, obj) {
                                let parent = obj.$parent.$parent;

                                if (parent.controls && parent.controls[7])  {
                                    let template;
                                    if (row.sendMessage === 1) {
                                        template = '<button class="list-operate" @click="sendMsg"><i class="hasIcon" style="margin-right:0.6em"><icon name="envelope"></icon></i></button>';
                                    } else {
                                        template = '<span style="color: #4daf51">已发[' + row.lastSendSMSTime + ']</span>';
                                    }
                                    return {
                                        template: template,
                                        methods: {
                                            sendMsg: function () {
                                              parent.sendMessage(index,row)
                                            }
                                        }
                                    }
                                } else {
                                    return  {
                                        template: '<div>--</div>'
                                    }
                                }
                            }
                        },
                        {title: '联系方式', width: '120', field: 'customerContact', textAlign: 'center'},
                        {title: '部门机构', width: '120', field: 'orgName', textAlign: 'center'},
                        {title: '品牌', width: '120', field: 'customerBrand', textAlign: 'center'},
                        {title: '采集日期', width: '140', field: 'collectDate', textAlign: 'center', sortable: false,  type: 'string', formatter: function (val) { if (val !== null && val !== '') {return val.substr(0, 19)} else {return '--'}}}
                        
                    ],
                    url: 'first/arrearageCustomers',
                    method: 'post',
//                     url: 'dist/json/sendMsg.json',
//                     method: 'get',
                    pageSize: 10,
                    pageNumber: 1,
                    sortName: 'readValue',
                    sortOrder: 'asc',
                    height: 300,
                    isExport: true,
                    exportURL: 'first/generateExcel'
                },
                showDialog: true
            }
        },
        props: [],
        methods: {
           sendMessage: function (index,row) {
             let that = this;
             for (let key in that.sendData) {
               that.sendData[key] = row[key];
             }
             let sysSet = JSON.parse(localStorage.systemConfig);
         
             that.sendData['companyName'] = sysSet.companyName;//企业的名称
             that.sendData['paymentAddr'] = sysSet.payAddr;//充值缴费地址
             //选择当前行
             that.arrearageRow.rowData = row;
             that.arrearageRow.rowIndex = index;
             let devTypeData = that.deviceType[row.deviceType] ;
             //  let devTypeData = that.deviceType['07'] ;
             let msgs = '尊敬的<span class="cautionMsgGre">' + row.customerName
               + '</span>用户： 您好，您的地址为' + row.customerAddr
               + '，截止' + row.collectDate + ' ，  剩余<span class="cautionMsgRed">' + row.readValue + '</span>元 。 请尽快到'
               + that.sendData.paymentAddr + '进行充值缴费 。【'
               + that.sendData.companyName + '】';
             that.confirms.show({
               msg: msgs,
               action: {
                   confirm: function () {
                       that.getSure()
                   }
               }
             });
           },
            getSure: function () {
                let that = this;
                let load  =  that.$refs.loadings;
                load.show({
                  msg: '正在发送短信，请稍后。。。'
                });
                that.getRequest({
                    url: 'system/sendMsg',
                    param: that.sendData,
                    method: 'post',
                    success: function (data) {
                      load.close();
                      that.$refs.caution.refreshTable(null);
                      that.alerts.show({
                        msg: data.message
                      });
//                        that.arrearageRow.sendMessage = 0;
//                        that.arrearageRow.lastSendSMSTime = data.data.sendTime;
//                        //更新数据源
//                        that.$refs.caution[0].updateRow(that.arrearageRow.rowIndex, that.arrearageRow);
//                      let Data = data.data;
//                      if (Data.result === 1) {
//                        //设置用户不能继续发送
//                        that.arrearageRow.sendMessage = 0;
//                        that.arrearageRow.lastSendSMSTime = Data.sendTime;
//                        //更新数据源
//                        that.$refs.caution[0].updateRow(that.arrearageRow.rowIndex, that.arrearageRow);
//                      } else {
//                        that.alerts.show({
//                          msg: data.message
//                        })
//                      }
                    },
                  error: function (data) {
                    load.close();
                    that.alerts.show({
                      msg: data.message
                    })
                  }
                })
            },
            closeDialog: function () {
                this.showDialog = false;
                this.$refs.sendMsg.close();
            },
            show: function () {
                let that = this;
                that.showDialog = true;
                setTimeout(function () {
                    that.$refs.sendMsg.show({
                      hasClose: true,
                      action: {
                        cancel: function () {
                          that.closeDialog()
                        }
                      }
                    });
                }, 0);
                that.$nextTick(function () {
                    if(that.$refs.caution) {
                        that.$refs.caution.refreshTable(null, 1);
                    }
                  
                })
            },
            init: function () {
                 this.showTable = true;
            }
        },
        computed: {
            alerts: function () {
                return this.$refs.alerts
            },
            confirms: function () {
             return this.$refs.confirms
           }
        },
        components: {},
        mounted: function () {
            let that = this;
            that.getAction('10001101')
          }
    }
</script>
<style lang="scss" scoped="scoped">
  @media screen and (max-width: 1700px) and (min-width: 1367px) {
    .dialogTable .table-tbody tbody tr td {
      padding: 0.6em 0.7em 0.6em 0.47em!important;
    }
  }

</style>




