<template >
    <div class="myDialog" >
      <dialogs type="customer" ref="userPass"  v-if="showDialog" width="35em" backColor="rgba(0,0,0,0.8)">
        <div slot="header">
           {{'用户身份验证'}}
        </div>
        <div slot="content">
            <ul class="dialog-ul" style="padding: 0.5em 0">
              <li v-for="(item, $index) in fromOpt ">
                <label class="label-show" >{{item.label}} </label>
                <inputs  v-if="item.type == 'input'" :option="item.options" @getInput="getForms" @enter="enter"></inputs>
              </li>
            </ul>
        </div>
        <div slot="footer">
          <button @click="moveOn" class="button-default">确定</button>
          <button @click="closeDialog" class="button-default cancel-button">取消</button>
        </div>
       </dialogs>
        <dialogs ref="alerts" type="alert"></dialogs>
        <encryptPass ref="encrypt"></encryptPass>
    </div>
</template>
<script>
    import encryptPass from './encryptPass.vue'
    export default {
        data () {
            return {
                userName: '',
                password: '',
                showDialog: false
            }
        },
        components: {
          encryptPass
        },
        methods: {
            getForms(val) {
               this.getOption(val, this);
            },
            enter: function (val) {
              this.getOption(val, this);
              if (this.userName !== '' && this.password !== ''){
                 this.moveOn()
              } else {
                    this.$refs.alerts.show({
                        msg: '用户名或密码都不能为空'
                    });
              }
            },
            moveOn: function () {
                let that = this;
                if (this.userName !== '' && this.password !== '') {
                   let pass = this.$refs.encrypt.encryptPass(this.password);
                   that.getRequest({
                        url: 'login',
                        param: {loginName: this.userName, password: pass},
                        method: 'post',
                        success: function (data) {
                          that.showDialog = false;
                          that.$refs.userPass.close();
                          that.$store.state.hasBlur = false;
                        },
                        error: function (data) {
                          that.$refs.alerts.show({
                            msg: data.message
                          });
                        }
                    }, true);
                } else {
                    that.$refs.alerts.show({
                        msg: '用户名或密码都不能为空'
                    });
                }
            },
            clearSession: function () {
               let that = this;
               that.getRequest({
                 url: 'logout',
                 method: 'post',
                 success: function () {}})
            },
            closeDialog: function () {
                let that = this;
                that.showDialog = false;
                that.$refs.userPass.close();
                that.$parent.powerOff();
                that.$store.state.hasBlur = false;  
            },
            show: function () {
                let that = this;
                //that.clearSession();
                that.password = '';
                that.showDialog = true;
                setTimeout(function () {
                  that.$store.state.hasBlur = true;  
                  that.$refs.userPass.show({
                    hasClose: true,
                    action: {
                        cancel: function () {
                            that.closeDialog()
                        }
                    }
                  });
                }, 0)
            }
        },
        mounted: function () {
        },
        computed: {
           fromOpt() {
             return [
                    {
                        type: 'input',
                        label: '用户名',
                        options: {
                            type: 'normalInput',
                            width: 'calc(100% - 7em)',
                            key: 'userName',
                            placeHolder: '请输入用户名',
                            length: 50
                        }
                    },
                    {
                        type: 'input',
                        label: '密码',
                        options: {
                            type: 'passwordInput',
                            width: 'calc(100% - 7em)',
                            key: 'password',
                            placeHolder: '请输入用户密码',
                            regular: 'password',
                            length: 22
                        }
                    }
                ]
           }
        },
        watch: {}
    }
</script>
<style lang="scss">
    .dialogContent {
        ul.bigForm {
            li {
                width: 100%;
                text-align: center;
                label, input, textarea {
                    float: left;
                    margin: 5px
                }
                label {
                    height: 35px;
                    line-height: 35px;
                    width: 100px;
                    text-align: right;
                }
                label + div.mySelect {
                    margin-left: 5px
                }
                textarea {
                    resize: none;
                    margin: 0;
                    border-radius: 4px
                }
                label.special-label {
                    width: 200px;
                    text-align: left;
                    font-weight: 100;
                }
            }
            li.double-input {
                width: auto;
                float: left;
                margin: 5px 0;
                label + div.mySelect {
                    margin-left: 0
                }
                label {
                    width: 70px;
                    margin: 0;
                }
                label.max-length {
                    width: 110px;
                    margin-right: 5px !important;
                }
                input, div.mySelect {
                    width: 190px;
                    margin: 0
                }
            }
        }
    }
   .littleTips{
    font-size: 0.94em;
    padding: 1em 0 0 0;
    color: #EC3935;
    text-align:left!important
   }
</style>



